
























import Vue from 'vue';
import {mapState} from 'vuex';

// Roma Components
import RomaOrganizer from '../components/RomaOrganizer.vue';
import RomaUnitList from '../components/RomaUnitList.vue';

export default Vue.extend({
    name: 'OrganizerPage',
    props: {},
    data() {
        return {};
    },
    computed: {
        ...mapState({
            school: (state) => {
                return state.organizer.selectedSchool;
            },
        }),
    },
    beforeRouteEnter (to, from, next) {
        next(vm => {
            if (vm.$store.state.organizer.selectedSchool) {
                return true;
            } else {
                return {name: 'home'};
            }

        });

    },
    components: {
        RomaOrganizer,
        RomaUnitList,
    },
});
